<template>
  <div id="video_wrap">
    <!--轮播-->
    <new-video-slider></new-video-slider>

    <!-- 视频分类Tab——Begin -->
    <div class="video_tab">
        <div class="search bar" v-if="searchShow">
          <div class="form">
          <input placeholder="请输入搜索" name="cname" type="text" v-model="label">
           <span class="seahchspan" @click="search()">搜索</span>
         </div>
         </div>

  <br/>
  <br/>
  <br/>


      <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
        <van-list
          v-model="loading"
          :finished="finished"
          @load="onLoad"
        >

          <ul>
            <li v-for="(item, index) in videoList" :key="index">

              <div @click="$router.push({
                                      path: '/videoDetail',
                                      query:{id:item.id}
                                   })">
                <img v-lazy="getImgURL(item.showImageUrl.thumbnailPelativePath)">
                <p class="time">{{ timeStamp(item.publishTime) }}</p>
              </div>
              <p class="tit text-overflow">{{ item.name }}</p>
            </li>
          </ul>

        </van-list>
        <div class="no_more" v-show="finished">没有更多了......</div>
      </van-pull-refresh>
    </div>
    <!-- 视频分类Tab——End -->
  </div>
</template>

<script>
    export default {
      data(){
        return{
          searchShow: true,
          value: '',
          videoList: [],//用于存放加载的数据
          loading: false,//控制上拉加载的加载动画
          finished: false,//控制在页面往下移动到底部时是否调用接口获取数据
          isLoading: false,//控制下拉刷新的加载动画
          pageNum: 1, // 页数
          offset: 0,  // 下次加载起始页
          totalPage: 0, // 总页数
          label:''
        }
      },

      computed: {
        filterResult() {
          return this.videoList.filter(value => new RegExp(this.value, 'i').test(value));
        }
      },
      methods:{
        async search() {
          let that = this;
          that.pageNum=1
          let params = {
            offset: (that.pageNum - 1) * 8,
            label: that.label
          };

          let res = await this.$http.get(this.$HOST + '/open/health/message/center/video/search/find', params);
          if(res.result.total == 0){
            that.videoList = []
          } else {

            that.videoList = res.result.data; // 第一页内容
            that.pageNum = res.result.page; // 当前显示页
            that.totalPage = Math.ceil(res.result.total / res.result.limit); // 总页数
            if(res.result.total<8) {
              that.finished = true; // 加载结束
              that.isLoading = false;
              that.loading = false;
              return false;
            }
          }
      },
        async init(){
          let that = this;
          let params = {
            offset: (that.pageNum - 1) * 8
          };
          let res = await this.$http.get(this.$HOST + '/open/health/message/center/app/videoList', params);
          if(res.result.videosAll.total == 0){
            this.searchShow = false
          } else {
            this.searchShow = true
          }
          that.videoList = res.result.videosAll.data; // 第一页内容
          that.pageNum = res.result.videosAll.page; // 当前显示页
          that.totalPage = Math.ceil(res.result.videosAll.total / res.result.videosAll.limit); // 总页数
           if(res.result.videosAll.total<8) {
             that.finished = true; // 加载结束
             that.isLoading = false;
             that.loading = false;
             return false;
          }
        },

        async concatData(){
          let that = this;
          that.pageNum += 1;
         // that.init();

          // 数据全部加载完成
          let params = {
            offset: (that.pageNum - 1) * 8
          };

          let res = await this.$http.get(this.$HOST + '/open/health/message/center/app/videoList', params);

          if(res.result.videosAll.total<8) {
            that.finished = true; // 加载结束
            that.isLoading = false;
            that.loading = false;
            return false;
          }


          that.videoList = that.videoList.concat(res.result.videosAll.data);
          this.loading = false;

          if(this.pageNum >= this.totalPage){
            that.finished = true; // 加载结束
            that.isLoading = false;
            that.loading = false;
          }
       },
      onRefresh() {
          setTimeout(() => {
            this.$toast({
              message: '刷新成功',
              position: 'bottom'
            });
            this.isLoading = false;
            this.pageNum = 1;
            this.loading = false;
            this.finished = false;
            this.isLoading = false;
            this.init();
          }, 500);
        },

        onLoad() {
          // 异步更新数据
          setTimeout(() => {
            this.concatData();
          }, 500);
        }
      },

      async created(){
        this.init();
      }
    }
</script>

<style scoped lang="scss">
  .no_more{
    font-size: 30px;
    color: #8d8d8d;
    text-align: center;
    margin: 50px auto 0;
  }

  #video_wrap{
    height: 100%;
    .video_tab{
      position: relative;
      top: 440px;
      z-index: 0;
      margin-bottom: 50px;

      ul{
        width: 90%;
        margin: auto;
        display: flex;
        flex-wrap: wrap;

        li{
          width: 47%;
          padding: 15px;
          margin: 25px 10px 0;
          background: #FFF;
          box-sizing: border-box;
          display: flex;
          flex-wrap: wrap;

          div{
            width: 100%;
            height: 180px;
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            justify-content: center;
            position: relative;

            p.time{
              position: absolute;
              bottom: 10px;
              right: 10px;
              color: #FFF;
            }

            img[lazy="error"],
            img[lazy="loading"]{
              width: 70%;
              height: 70%;
            }
          }

          p.tit{
            padding-top: 10px;
            font-size: 25px;
          }
        }
      }
    }
  }



  div.search {
     padding:10px 0;
     position: fixed;
     top: 6rem;
     z-index: 9999;
     width: 100%;
     background: #ffffff;
    left: -4px;
  }

  .form {
    position:relative;
    width:700px;
    margin:2px auto;
 }

 .search input,button {

    border:none;

    outline:none;

  }

 .search input {
   width:100%;
   height:70px;
   padding-left:13px;
   font-size: 24px;
 }

  .search input {
    border:2px solid #c5464a;
    border-radius:5px;
    background:transparent;
    top:0;
    right:0;
  }

   .seahchspan {
     height:75px;
     cursor:pointer;
     position:absolute;
     line-height: 70px;
     background:#c5464a;
     border-radius:0 5px 5px 0;
     width:120px;
     top:0;
     right:-19px;
     color: #ffffff;
     text-align: center;
     font-size: 24px;
 }

</style>



